import React from "react";
import * as Yup from "yup";
import {
  Box,
  Card,
  Typography,
  MenuItem,
  Button,
  FormControl,
  Select,
  InputLabel,
  TextField,
  RadioGroup,
  FormControlLabel,
  Radio,
  Divider,
} from "@material-ui/core";
import { withStyles, makeStyles } from "@material-ui/core/styles";
import Table from "@material-ui/core/Table";
import TableBody from "@material-ui/core/TableBody";
import TableCell from "@material-ui/core/TableCell";
import TableContainer from "@material-ui/core/TableContainer";
import TableHead from "@material-ui/core/TableHead";
import TableRow from "@material-ui/core/TableRow";
import Paper from "@material-ui/core/Paper";
import { Formik } from "formik";
import { useState, useEffect } from "react";
import axios from "axios";
import { Section } from "../mangement/HelperFunction";
import { green, yellow } from "@material-ui/core/colors";
import { TableRowsTwoTone } from "@material-ui/icons";
import toast from "react-hot-toast";
import userService from "./apis/userService";
import customAxios from "./apis/Api";

const StyledTableCell = withStyles((theme) => ({
  head: {
    backgroundColor: theme.palette.common.black,
    color: theme.palette.common.white,
  },
}))(TableCell);

const StyledTableRow = withStyles((theme) => ({
  root: {
    "&:nth-of-type(odd)": {
      backgroundColor: theme.palette.action.hover,
    },
  },
}))(TableRow);

const ColorButton = withStyles((theme) => ({
  root: {
    color: theme.palette.getContrastText(yellow[900]),
    backgroundColor: yellow[900],
    "&:hover": {
      backgroundColor: yellow[700],
    },
  },
  margin: {
    margin: theme.spacing(1),
  },
}))(Button);

const useStyles = makeStyles({
  table: {
    minWidth: 700,
    // marginTop:10
  },

  feild: {
    width: "30%",
    marginLeft: 5,
    marginBottom: 10,
  },
});

const Student_PromotionForm = (props) => {
  const { Data, studentPass, handleComplete, section, classRoom } = props;
  const classes = useStyles();
  // const [classRoom, setClassRoom] = useState([]);
  // const [section, setSection] = useState([]);
  const [session, setSession] = useState([]);
  // const data = Object.values(Data);

  const Session = async () => {
    const sessionData = await customAxios().get(
      "http://localhost:8000/promtion/session"
    );
    setSession(sessionData.data);
  };

  useEffect(() => {
    // const fun = Section("class/api");
    // fun.then((res) => {
    //   setClassRoom(res);
    // });
    // const fun1 = Section("class/api/section");
    // fun1.then((res) => {
    //   setSection(res);
    // });

    Session();
  }, []);

  return (
    <>
      <Formik
        initialValues={{
          session: "",
          classid: "",
          sectionid: "",
          // submit: null,
        }}
        validationSchema={Yup.object().shape({})}
        onSubmit={async (values, { setErrors, setStatus, setSubmitting }) => {
          // alert(JSON.stringify(values), null);

          try {
            if (handleComplete) {
              handleComplete(values);
            }
          } catch (err) {
            console.error(err);
            // setError(err.message);
          }
        }}
      >
        {({
          errors,
          handleBlur,
          handleChange,
          handleSubmit,
          isSubmitting,
          setFieldValue,
          setFieldTouched,
          touched,
          values,
        }) => (
          <form onSubmit={handleSubmit}>
            {Data.length > 0 && (
              <>
                <Box
                  sx={{
                    mt: 4,
                  }}
                >
                  <Divider>
                    <Typography color="textPrimary" variant="h6">
                      Promote Student to Next Session
                    </Typography>
                  </Divider>
                </Box>

                <Box
                  sx={{
                    mt: 2,
                    display: "flex",
                    alignItems: "center",
                    justifyContent: "center",
                  }}
                >
                  <FormControl className={classes.feild}>
                    {/* <InputLabel id="classid">Class</InputLabel> */}
                    <TextField
                      fullWidth
                      onChange={handleChange}
                      onFocus={handleChange}
                      labelId="session"
                      select
                      SelectProps={{ native: true }}
                      value={values.session}
                      label="session"
                      id="session"
                      name="session"
                    >
                      {session.map((option) => (
                        <option key={option.id} value={option.id}>
                          {option.year}
                        </option>
                      ))}
                    </TextField>
                  </FormControl>
                  <FormControl className={classes.feild}>
                    {/* <InputLabel id="classid">Class</InputLabel> */}
                    <TextField
                      fullWidth
                      onChange={handleChange}
                      onFocus={handleChange}
                      labelId="classid"
                      select
                      SelectProps={{ native: true }}
                      value={values.classid}
                      label="Class"
                      id="classid"
                      name="classid"
                    >
                      {classRoom.map((option) => (
                        <option key={option.id} value={option.id}>
                          {option.className}
                        </option>
                      ))}
                    </TextField>
                  </FormControl>
                  <FormControl className={classes.feild}>
                    <InputLabel id="sectionid">Section</InputLabel>

                    <TextField
                      fullWidth
                      labelId="sectionid"
                      select
                      SelectProps={{ native: true }}
                      value={values.sectionid}
                      onFocus={handleChange}
                      onChange={handleChange}
                      id="sectionid"
                      label="Section"
                      name="sectionid"
                    >
                      {section.map((option) => (
                        <option key={option.id} value={option.id}>
                          {option.name}
                        </option>
                      ))}
                    </TextField>
                  </FormControl>
                  <Button
                    sx={{ mt: 1, mr: 2, ml: 2 }}
                    color="primary"
                    disabled={isSubmitting}
                    type="submit"
                    variant="contained"
                  >
                    Promote
                  </Button>
                </Box>
              </>
            )}
          </form>
        )}
      </Formik>
      <TableContainer component={Paper}>
        <Table className={classes.table} aria-label="customized table">
          <TableHead>
            <TableRow>
              <StyledTableCell>Roll No</StyledTableCell>
              <StyledTableCell>Name</StyledTableCell>
              <StyledTableCell>Class</StyledTableCell>
              <StyledTableCell>Section</StyledTableCell>
              <StyledTableCell>Date Of Birth</StyledTableCell>
              <StyledTableCell>Current Result</StyledTableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {Data.length > 0 &&
              Data.map((row) => (
                <StyledTableRow key={row.id}>
                  <StyledTableCell component="th" scope="row">
                    {row.rollno}
                  </StyledTableCell>
                  <StyledTableCell align="start">{`${row.name} `}</StyledTableCell>
                  <StyledTableCell align="start">
                    {row.className}
                  </StyledTableCell>
                  <StyledTableCell align="start">{row.section}</StyledTableCell>
                  <StyledTableCell align="start">{row.dob}</StyledTableCell>
                  <StyledTableCell align="start">
                    {" "}
                    <FormControl
                      style={{ paddingLeft: 10 }}
                      component="fieldset"
                    >
                      <RadioGroup
                        row
                        aria-label="picked"
                        name="picked"
                        id="picked"
                        // value={values.picked}
                        // onChange={handleChange}
                      >
                        <FormControlLabel
                          value="pass"
                          onChange={() => {
                            studentPass(row, 1);
                          }}
                          control={<Radio />}
                          label="pass"
                        />
                        <FormControlLabel
                          value="fail"
                          onChange={() => {
                            studentPass(row, 0);
                          }}
                          control={<Radio />}
                          label="Fail"
                        />
                      </RadioGroup>
                    </FormControl>
                  </StyledTableCell>
                </StyledTableRow>
              ))}
          </TableBody>
        </Table>
      </TableContainer>
    </>
  );
};

export default Student_PromotionForm;
