import React from "react";
import * as Yup from "yup";
import {
  Box,
  Card,
  Typography,
  Button,
  FormControl,
  InputLabel,
  TextField,
} from "@material-ui/core";
import { makeStyles } from "@material-ui/core/styles";

import { Formik } from "formik";
import { useState, useEffect } from "react";
import axios from "axios";
import { Section } from "../mangement/HelperFunction";

import toast from "react-hot-toast";
import Student_PromotionForm from "./Student_PromotionForm";
import userService from "./apis/userService";
import {customAxios} from "./apis/Axios";

const useStyles = makeStyles({
  table: {
    minWidth: 700,
    // marginTop:10
  },

  feild: {
    width: "30%",
    marginLeft: 5,
  },
});

const Attendence_List = () => {
  const classes = useStyles();
  const [classRoom, setClassRoom] = useState([]);
  const [section, setSection] = useState([]);
  const [Data, setData] = useState([]);
  const [studentData, setStudentData] = useState([]);

  const handleComplete = async (values) => {
    const studentDatacopy = [...studentData];

    const combine = {
      ...values,
      studentDatacopy: [...studentDatacopy],
    };
    await customAxios.postCall("promtion/student-insert", {
        ...combine,
      })
      .then((res) => {
        toast.success("Student Promote successfully")
        console.log(res);
      }).catch(e=>{
        console.log(e)
      });
      console.log(combine)
  };
  const studentPass = (student, pass_Fail) => {
    console.log(student);
    const studentDataCopy = [...studentData];
    let obj = {
      F_phone: student.F_phone,
      addmissionid: student.addid,
      f_Email: student.f_Email,
      father_CNIC: student.father_CNIC,
      fdesignation: student.fdesignation,
      permanent_adress: student.permanent_adress,
      sectionid: student.sectionid,
      zipcode: student.zipcode,
      Student_id: student.id,
      student_name: student.name,
      father_name: student.fname,
      faccupation: student.faccupation,
      FatherMonthly_income: student.FatherMonthly_income,
      Relation_with_father: student.Relation_with_father,
      dob: student.dob,
      gender: student.gender,
      class: student.className,
      classid:student.classid,
      section: student.section,
      religion: student.religion,
      addmissionDate: student.admissiondate,
      phone: student.phone,
      email: student.email,
      address1: student.address1,
      address2: student.address2,
      studentCNIC: student.studentCNIC,
      rollno: student.rollno,
      balance: student.Balance,
      pass_Fail: pass_Fail,
    };

    const findIndex = studentDataCopy.findIndex((res) => {
      return res.Student_id == student.id;
    });
    if (findIndex >= 0) {
      studentDataCopy[findIndex] = obj;
    } else {
      studentDataCopy.push({ ...obj });
    }
    setStudentData(studentDataCopy);
    // console.log("studentDataCopy", studentDataCopy);
  };
  useEffect(() => {
    const fun = Section("class/api");
    fun.then((res) => {
      setClassRoom(res);
    });
    const fun1 = Section("class/api/section");
    fun1.then((res) => {
      setSection(res);
    });
  }, []);

  // useEffect(async () => {
  //   console.log("SutdentData", Data);
  //   if (Data) {
  //     await axios
  //       .post("http://localhost:8000/promtion/student-insert",
  //         [...Data],
  //       )
  //       .then((res) => {
  //         console.log(res.data);
  //       });
  //   }
  // }, [Data]);
  if (!Data) return null;
  return (
    <>
      <Formik
        initialValues={{
          classid: "",
          sectionid: "",
          // submit: null,
        }}
        validationSchema={Yup.object().shape({})}
        onSubmit={async (values, { setErrors, setStatus, setSubmitting }) => {
          // try {
          // alert(JSON.stringify(values), null);
          // Call API to store step data in server session
          // return;
          const data = { ...values };
          if (data.classid && data.sectionid == "") {
            toast.error("Please Fill All Feild");
          } else {
            // console.log(data);
            // await new Promise((r) => setTimeout(r, 500));
            await customAxios.getCalls("promtion/StudentData", {
                params: {
                  ...data,
                },
              })
              .then((res) => {
                setData(res);
              });
          }
        }}
      >
        {({
          errors,
          handleBlur,
          handleChange,
          handleSubmit,
          isSubmitting,
          setFieldValue,
          setFieldTouched,
          touched,
          values,
        }) => (
          <form onSubmit={handleSubmit}>
            <Card sx={{ p: 3 }}>
              <Typography color="textPrimary" variant="h6">
                Student Promotion
              </Typography>
              <Box sx={{ mt: 2 }}>
                <FormControl className={classes.feild}>
                  {/* <InputLabel id="classid">Class</InputLabel> */}
                  <TextField
                    fullWidth
                    onChange={handleChange}
                    onFocus={handleChange}
                    labelId="classid"
                    select
                    SelectProps={{ native: true }}
                    value={values.classid}
                    label="Class"
                    id="classid"
                    name="classid"
                  >
                    {classRoom.map((option) => (
                      <option key={option.id} value={option.id}>
                        {option.className}
                      </option>
                    ))}
                  </TextField>
                </FormControl>
                <FormControl className={classes.feild}>
                  <InputLabel id="sectionid">Section</InputLabel>

                  <TextField
                    fullWidth
                    labelId="sectionid"
                    select
                    SelectProps={{ native: true }}
                    value={values.sectionid}
                    onFocus={handleChange}
                    onChange={handleChange}
                    id="sectionid"
                    label="Section"
                    name="sectionid"
                  >
                    {section.map((option) => (
                      <option key={option.id} value={option.id}>
                        {option.name}
                      </option>
                    ))}
                  </TextField>
                </FormControl>
                <Button
                  sx={{ mt: 1, mr: 2, ml: 2 }}
                  color="primary"
                  disabled={isSubmitting}
                  type="submit"
                  variant="contained"
                >
                  Select
                </Button>
              </Box>
            </Card>
          </form>
        )}
      </Formik>

      <Student_PromotionForm
        handleComplete={handleComplete}
        studentPass={studentPass}
        Data={Data}
        section={section}
        classRoom={classRoom}
      />
    </>
  );
};
export default Attendence_List;
